<template>
  <div class="index">
    <div class="top">
      <div class="top-flex">
        <div class="left">
          <div class="name">{{shopDetail.shopName}}</div>
          <div class="flex">
            <img class="icon" src="@/assets/img/merchantsIcon/address.png" alt />
            <div class="text">{{shopDetail.shopAddr}}</div>
          </div>
          <div class="flex" @click="show = true">
            <img class="icon" src="@/assets/img/merchantsIcon/telno.png" alt />
            <div class="text">
              <div>联系商家</div>
              <div id="telno" style="opacity:0;position:absolute;">{{shopDetail.shopMobile}}</div>
              <img class="icon" src="@/assets/img/merchantsIcon/right.png" alt />
            </div>
          </div>
        </div>
        <div class="right">
          <img :src="shopDetail.shopHeader?$utils.dealImg(shopDetail.shopHeader):require('@/assets/img/default/userDefault.png')" alt />
        </div>
      </div>
      <button style="display:none" ref="btn" class="copy-btn" data-clipboard-action="copy" data-clipboard-target="#telno" @click="copy"></button>
      <div class="tabs">
        <div class="tab" style="border-right:1pt solid #efefef;" @click="toggle('1')">
          <img class="icon" :src="active == '1'?require('@/assets/img/merchantsIcon/icon4.png'):require('@/assets/img/merchantsIcon/icon3.png')" alt />
          <p class="text">下单</p>
        </div>
        <div class="tab" @click="toggle('2')">
          <img class="icon" :src="active == '2'?require('@/assets/img/merchantsIcon/icon2.png'):require('@/assets/img/merchantsIcon/icon1.png')" alt />
          <p class="text">我的订单</p>
        </div>
      </div>
    </div>
    <div class="mid">
      <div class="goodList" v-show="active == '1'">
        <div class="flex" v-if="cateList.length">
          <div class="left mescroll">
            <van-sidebar v-model="activeKey" @change="onChange">
              <van-sidebar-item v-for="(item,index) in cateList" :key="index" :title="item.cateName" />
            </van-sidebar>
          </div>
          <div class="right mescroll" ref="right">
            <div :ref="'cate'+index" v-for="(item,index) in cateList" :key="index">
              <div class="title">{{item.cateName}}</div>
              <div class="list">
                <div class="item" v-for="(item1,index1) in item.goodsList" :key="index1">
                  <div class="item-left">
                    <img :src="item1.gimg?$utils.dealImg(item1.gimg):require('@/assets/img/default/defaultZ.png')" alt />
                  </div>
                  <div class="item-right">
                    <p class="name">{{item1.gname}}</p>
                    <div class="flex">
                      <p class="price">
                        <span>￥</span>
                        {{item1.goutPrice.toFixed(2)}}
                      </p>
                      <div class="num">
                        <div class="minus" v-if="item1.num" @click="min(item1)">-</div>
                        <div class="value" v-if="item1.num">{{item1.num}}</div>
                        <div class="add" :style="item1.num?'':'background:#999'" @click="add(item1)">+</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="nothing" v-else>
          <img src="../../assets/img/nothing.png" alt="">
          <p>~店家正在努力上新中~</p>
        </div>
        <div class="bottom" v-if="cateList.length">
          <div class="bottom-flex">
            <div class="icon">
              <p class="label">{{num}}</p>
              <img src="@/assets/img/merchantsIcon/icon.png" alt />
            </div>
            <div class="allPrice">
              <span>合计:</span>
              <span style="font-weight:bold;">￥</span>
              <span style="font-size:20px;font-weight:bold;">{{price.toFixed(2)}}</span>
            </div>
            <div class="bottom-right">
              <div class="btn" @click="submit">我选好了</div>
            </div>
          </div>
        </div>
      </div>
      <div class="orderList" v-show="active == '2'">
        <div class="list mescroll" id="list">
          <div class="list-data" id="listData">
            <div class="item" v-for="(item,index) in orderList" :key="index">
              <div class="item-top flex">
                <div class="No flex-2">订单编号:{{item.orderno}}</div>
                <div class="status" :style="item.orderStatus == 1 || item.orderStatus == 2?'color:#eb5252':item.orderStatus == 3?'color:#333':'color:#999'">{{$utils.getText(item.orderStatus)}}</div>
              </div>
              <div>
                <div class="item-goods" v-for="(item1,index1) in item.scEcomOrdersDetailList" :key="index1">
                  <div class="shopName">{{item1.scEcomGoods?item1.scEcomGoods.gname:''}}</div>
                  <div class="num">×{{item1.gorderCount}}</div>
                  <div class="price">￥{{item1.scEcomGoods?item1.scEcomGoods.goutPrice.toFixed(2):'0.00'}}</div>
                </div>
              </div>
              <div class="orderInfo">
                <div class="price">共{{item.shopCount}}件商品 总计：¥{{item.oorderMoneyShifu.toFixed(2)}}</div>
              </div>
              <div class="item-bottom">
                <div class="btn" @click="toDetail(item.id)">订单详情</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <van-action-sheet v-model="show" :actions="actions" @select="onSelect" cancel-text="取消" @cancel="show = false" />
  </div>
</template>
<script>
import { Sidebar, SidebarItem, ActionSheet } from "vant";
import {
  getShopIndex,
  addShoppingCart1,
  deleteCart1,
  reviseCartNumber1,
  getOrderList1
} from "@/api";
import MeScroll from "mescroll.js";
import ClipboardJS from "clipboard";
export default {
  components: {
    [Sidebar.name]: Sidebar,
    [SidebarItem.name]: SidebarItem,
    [ActionSheet.name]: ActionSheet
  },
  data() {
    return {
      active: "1",
      activeKey: 0,
      show: false,
      actions: [
        { name: "复制手机号", subname: "通过手机号加老板微信", value: 1 },
        { name: "拨打手机号", value: 2 }
      ],
      cateList: [],
      cartList: [],
      num: 0,
      price: 0,
      shopDetail: {},
      mescroll: null,
      orderList: []
    };
  },
  methods: {
    toggle(type) {
      this.active = type;
      if (type == "2" && !this.mescroll) {
        setTimeout(() => {
          this.startMescroll();
        });
      }
    },
    onChange() {
      this.$refs.right.scrollTop =
        this.$refs["cate" + this.activeKey][0].offsetTop - 180;
    },
    min(item) {
      if (item.num > 0) {
        if (item.num > 1) {
          this.revise(item, "min");
        } else {
          this.del(item);
        }
      }
    },
    add(item) {
      if (item.num > 0) {
        this.revise(item, "add");
      } else {
        this.addCart(item);
      }
    },
    addCart(item) {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      addShoppingCart1({
        gcode: item.id,
        gorderCount: 1,
        shopCode: this.$route.query.shopCode
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          item.num += 1;
          this.$toast("修改成功");
          this.total();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    revise(item, type) {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      reviseCartNumber1({
        id: item.id,
        shopNum: type == "add" ? item.num + 1 : item.num - 1,
        shopCode: this.$route.query.shopCode
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          type == "add" ? (item.num += 1) : (item.num -= 1);
          this.$toast("修改成功");
          this.total();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    del(item) {
      deleteCart1({
        id: item.id,
        shopCode: this.$route.query.shopCode
      }).then(res => {
        if (res.code == 0) {
          item.num -= 1;
          this.$toast("修改成功");
          this.total();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    onSelect(e) {
      console.log(e);
      if (e.value == 1) {
        this.$refs.btn.click();
        this.show = false;
      } else {
        window.location.href = `tel://${this.shopDetail.shopMobile}`;
        this.show = false;
      }
    },
    copy() {
      let that = this;
      var clipboard = new ClipboardJS(".copy-btn");
      clipboard.on("success", e => {
        that.$toast("复制成功");
        clipboard.destroy();
      });
      clipboard.on("error", e => {
        that.$toast("复制失败");
        clipboard.destroy();
      });
    },
    submit() {
      let goodList = [];
      this.cateList.map(item => {
        item.goodsList.map(item1 => {
          if (item1.num) {
            goodList.push({
              id: item1.id,
              gname: item1.gname,
              gimg: item1.gimg,
              goutPrice: item1.goutPrice,
              num: item1.num
            });
          }
        });
      });
      sessionStorage.setItem("goodsList", JSON.stringify(goodList));
      this.$router.push({
        name: "merchantsConfirmOrder",
        query: {
          shopCode: this.$route.query.shopCode
        }
      });
    },
    total() {
      this.num = 0;
      this.price = 0;
      this.cateList.map(item => {
        item.goodsList.map(item1 => {
          this.num += item1.num;
          this.price += item1.num * item1.goutPrice;
        });
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getShopIndex({
        shopCode: this.$route.query.shopCode
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          res.data.cateList.map((item, index) => {
            item.goodsList.map((item1, index1) => {
              item1.num = 0;
              res.data.cartList.map((item2, index2) => {
                if (item1.gcode == item2.scEcomGoods.gcode) {
                  item1.num = item2.gorderCount;
                }
              });
            });
          });
          this.cateList = res.data.cateList;
          this.shopDetail = res.data.shop;
          this.total();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    toDetail(id) {
      this.$router.push({
        name: "userOrderDetail",
        query: { id: id }
      });
    },
    loadList(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        shopCode: this.$route.query.shopCode,
        pageNum: pageNum,
        pageSize: pageSize
      };
      getOrderList1(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadList(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.orderList = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.orderList.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = this.$config.title;
    sessionStorage.removeItem("address");
    sessionStorage.removeItem("goodsList");
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
