  height: 100vh;
  background: #fff;
  .top {
    background: #eb514f;
    .top-flex {
      display: flex;
      align-items: center;
      padding: 30px 20px;
      .left {
        flex: 2;
        color: #fff;
        .name {
          font-size: 18px;
          line-height: 40px;
        }
        .flex {
          color: #ffd4d3;
          display: flex;
          font-size: 13px;
          align-items: center;
          line-height: 30px;
          .icon {
            width: 15px;
            margin-right: 5px;
          }
          .text {
            flex: 2;
            display: flex;
            align-items: center;
            .icon {
              margin-left: 2px;
              width: 12px;
            }
          }
        }
      }
      .right {
        img {
          width: 70px;
          height: 70px;
          border-radius: 50%;
        }
      }
    }
    .tabs {
      display: flex;
      align-items: center;
      background: #fff;
      border-radius: 5px 5px 0 0;
      height: 60px;
      border-bottom: 1pt solid #eee9eb;
      box-sizing: border-box;
      .tab {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        .icon {
          width: 20px;
          margin-right: 5px;
        }
        .text {
          font-size: 15px;
          line-height: 20px;
        }
      }
    }
  }
  .mid {
    background: #fff;
    .goodList {
      height: calc(100vh - 130px - 60px - 100px);
      .flex {
        display: flex;
        .left {
          width: 100px;
          height: calc(100vh - 130px - 60px - 100px);
          background: #f2f2f2;
        }
        .right {
          height: calc(100vh - 130px - 60px - 100px);
          flex: 2;
          padding: 0 10px;
          .title {
            font-size: 14px;
            line-height: 30px;
            padding: 10px 0 5px;
          }
          .list {
            .item {
              display: flex;
              align-items: center;
              padding: 10px 0;
              border-bottom: 1pt solid #eee;
              .item-left {
                img {
                  width: 60px;
                  height: 60px;
                  border-radius: 5px;
                  margin-right: 10px;
                }
              }
              .item-right {
                flex: 2;
                .name {
                  line-height: 20px;
                  margin-bottom: 5px;
                  font-weight: bold;
                }
                .flex {
                  display: flex;
                  height: 20px;
                  .price {
                    font-size: 14px;
                    color: #eb514f;
                    font-weight: bold;
                    flex: 1;
                    span {
                      font-size: 12px;
                    }
                  }
                  .num {
                    display: flex;
                    flex: 1;
                    justify-content: flex-end;
                    .minus {
                      width: 20px;
                      height: 20px;
                      line-height: 16px;
                      border-radius: 50%;
                      background: #eb514f;
                      text-align: center;
                      color: #fff;
                    }
                    .value {
                      text-align: center;
                      flex: 2;
                    }
                    .add {
                      width: 20px;
                      height: 20px;
                      line-height: 18px;
                      border-radius: 50%;
                      background: #eb514f;
                      text-align: center;
                      color: #fff;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .nothing {
      text-align: center;
      padding-top: calc((100vh - 160px - 60px - 71px - 130px) / 2);
      img {
        width: 100px;
      }
      p {
        font-size: 13px;
        color: #999;
        line-height: 30px;
      }
    }
    .orderList {
      height: calc(100vh - 160px - 60px);
      .list {
        border-top: 1pt solid #efefef;
        .item {
          padding: 15px 10px;
          border-bottom: 1pt solid #efefef;
          background: #fff;
          .flex {
            display: flex;
            .flex-1 {
              flex: 1;
            }
            .flex-2 {
              flex: 2;
              position: relative;
            }
          }
          .item-top {
            font-size: 15px;
            line-height: 20px;
            .No {
              color: #333;
            }
          }
          .item-goods {
            display: flex;
            align-items: center;
            font-size: 13px;
            line-height: 20px;
            padding: 8px 0;
            .shopName {
              flex: 2;
            }
            .num {
              width: 30px;
