}
</style>
<style lang="scss">
.index {
  .van-sidebar {
    width: 100px;
  }
  .van-sidebar-item {
    background: #f2f2f2;
    border-bottom: 1pt solid #e5e5e5;
  }
  .van-sidebar-item--select {
    background: #fff;
  }
  .van-sidebar-item__text {
    font-size: 16px;
  }
  .van-sidebar-item--select .van-sidebar-item__text {
    color: #eb514f;
  }
  .mescroll-empty {
    transform: translateY(13%);
  }
}
</style>